import { makeStyles } from '@material-ui/core/styles';

export default makeStyles((theme) => ({
  AppBar: {
    borderRadius: 15,
    margin: '30px 0',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#2F2F2B',
  },
  heading: {
    color: '#FF7129',
  },
  image: {
    height: '200px',
  },
  [theme.breakpoints.down('sm')] : {
    mainContainer: {
      flexDirection: "column-reverse",
    }
  }
}));